.login__button{
  width: 358px;
  height: 50px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 191px;
  background: #FFFFFF;
  border-radius: 2px;
  color: black;

  font-family: 'Inter', Arial, sans-serif;;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
}

.login__button:hover{
  opacity: 0.85;
  transition: opacity 0.5s ease;
  cursor: pointer;
}

.login__button:disabled{
  opacity: 1;
  transition:none;
  cursor:auto;
  color: rgba(16, 16, 16, 0.3)
}